import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'lib-message-dialog',
  templateUrl: './message-dialog.component.html',
  styleUrls: ['./message-dialog.component.css']
})
export class MessageDialogComponent  {
  @Input() id: String = '';
  @Input() visible: Boolean = false;
  @Input() content: String = '';
  @Input() buttons: any = [];
  @Input() showHeader: Boolean = false;
  @Input() headerContent: String = '';
  @Input() closable: Boolean = false;
  @Input() noOfButton: Number = 0;
  @Input() index: Number = 0;
  @Input() draggable: Boolean = false;
  @Input() resizable: Boolean = false;
  @Input() baseZIndex: Number = 1000;
  @Input() style: Object = { width: '50vw' };
  @Input() closeOnEscape: Boolean = false;
  @Output() cancle: EventEmitter<any> = new EventEmitter<any>();
  @Output() buttonClick: EventEmitter<any> = new EventEmitter<any>();

  cancel() {
    this.cancle.emit();
  }
  onClickButtons(value) {
    this.buttonClick.emit(value);
  }

}
